#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.iconImg{
    height: 50px;
    width: 50px;
    margin-left: 10px;
}
.bg{
    position: absolute;
    top: 3rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 80vh;
}
.introContent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 1.75rem;
    font-weight: 100;
}
.introName{
    color: yellow;
}
.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
 }
 .btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}
.btn:hover{
   background-color: yellow;
}
.btnImg{
     object-fit: cover;
     margin: 0 0.25rem;
     height: 1rem;
}

@media screen and (max-width:840px) {
    #intro{
        background:linear-gradient(rgba(255, 255, 255, 0.119),rgba(255, 255, 255, 0.277))
    }
    .bg{
        right:-10vw
    }
    .introContent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}
@media(max-width: 448px){
    .introPara, .hello{
        flex-direction: column;
        padding: 8% 0 ;
        color: black;
        font-weight: 800;
        
    }
    .introContent{
        margin-top: 10px;
    }

    
}